// ------------------------------- node builder ------------------------------ //
const builder = {
    translateArgs: (pdNode) => ({
        frequency: assertOptionalNumber(pdNode.args[0]) || 0,
    }),
    build: () => ({
        inlets: {
            '0': { type: 'signal', id: '0' },
            '1': { type: 'message', id: '1' },
        },
        outlets: {
            '0': { type: 'signal', id: '0' },
        },
    }),
    configureMessageToSignalConnection: (inletId, { frequency }) => {
        if (inletId === '0') {
            return { initialSignalValue: frequency };
        }
        return undefined;
    },
};
const makeNodeImplementation = ({ name, coeff, generateOperation, }) => {
    // ------------------------------ generateDeclarations ------------------------------ //
    const variableNames = generateVariableNamesNodeType(name, [
        'setPhase'
    ]);
    const nodeCore = () => Sequence([
        Class(variableNames.stateClass, [
            Var('Float', 'phase'),
            Var('Float', 'J'),
        ]),
        Func(variableNames.setPhase, [
            Var(variableNames.stateClass, 'state'),
            Var('Float', 'phase'),
        ], 'void') `
            state.phase = phase % 1.0${coeff ? ` * ${coeff}` : ''}
        `
    ]);
    const initialization = ({ globs, state }) => ast `
            ${ConstVar(variableNames.stateClass, state, `{
                phase: 0,
                J: 0,
            }`)}
            
            commons_waitEngineConfigure(() => {
                ${state}.J = ${coeff ? `${coeff}` : '1'} / ${globs.sampleRate}
            })
        `;
    // ------------------------------- loop ------------------------------ //
    const loop = ({ ins, state, outs }) => ast `
        ${outs.$0} = ${generateOperation(`${state}.phase`)}
        ${state}.phase += (${state}.J * ${ins.$0})
    `;
    // ------------------------------- messageReceivers ------------------------------ //
    const messageReceivers = ({ globs, state }) => ({
        '1': coldFloatInletWithSetter(variableNames.setPhase, state),
    });
    return {
        initialization: initialization,
        messageReceivers: messageReceivers,
        loop: loop,
        dependencies: [
            commonsWaitEngineConfigure,
            nodeCore
        ]
    };
};
// ------------------------------------------------------------------- //
const nodeImplementations = {
    'osc~': makeNodeImplementation({
        name: 'osc_t',
        coeff: '2 * Math.PI',
        generateOperation: (phase) => `Math.cos(${phase})`
    }),
    'phasor~': makeNodeImplementation({
        name: 'phasor_t',
        generateOperation: (phase) => `${phase} % 1`
    }),
};
const builders = {
    'osc~': builder,
    'phasor~': builder,
};

export { builders, nodeImplementations };
