// ------------------------------- node builder ------------------------------ //
const builder = {
    translateArgs: () => ({}),
    build: () => ({
        inlets: {
            '0': { type: 'signal', id: '0' },
            '0_message': { type: 'message', id: '0_message' },
        },
        outlets: {
            '0': { type: 'message', id: '0' },
        },
        isPullingSignal: true,
    }),
    configureMessageToSignalConnection: (inletId) => {
        if (inletId === '0') {
            return { reroutedMessageInletId: '0_message' };
        }
        return undefined;
    },
};
// ------------------------------- generateDeclarations ------------------------------ //
const variableNames = generateVariableNamesNodeType('snapshot_t');
const nodeCore = () => Sequence([
    Class(variableNames.stateClass, [
        Var('Float', 'currentValue')
    ]),
]);
const initialization = ({ state }) => ast `
        ${ConstVar(variableNames.stateClass, state, `{
            currentValue: 0
        }`)}
    `;
// ------------------------------- loop ------------------------------ //
const loop = ({ ins, state }) => ast `
    ${state}.currentValue = ${ins.$0}
`;
// ------------------------------- messageReceivers ------------------------------ //
const messageReceivers = ({ state, snds }) => ({
    '0_message': AnonFunc([Var('Message', 'm')], 'void') `
        if (msg_isBang(m)) {
            ${snds.$0}(msg_floats([${state}.currentValue]))
            return 
        }
    `,
});
// ------------------------------------------------------------------- //
const nodeImplementation = {
    loop: loop,
    messageReceivers: messageReceivers,
    initialization: initialization,
    dependencies: [
        bangUtils,
        nodeCore,
    ]
};

export { builder, nodeImplementation };
