// ------------------------------- node builder ------------------------------ //
const builderSend = {
    translateArgs: (pdNode) => ({
        busName: assertOptionalString(pdNode.args[0]) || '',
    }),
    build: () => ({
        inlets: {
            '0': { type: 'message', id: '0' },
            '1': { type: 'message', id: '1' },
        },
        outlets: {},
    }),
};
const builderReceive = {
    translateArgs: (pdNode) => ({
        busName: assertOptionalString(pdNode.args[0]) || '',
    }),
    build: () => ({
        inlets: {},
        outlets: {
            '0': { type: 'message', id: '0' },
        },
        isPushingMessages: true
    }),
};
// ------------------------------- generateDeclarations ------------------------------ //
const variableNames = generateVariableNamesNodeType('send');
const nodeCore = () => Sequence([
    Class(variableNames.stateClass, [
        Var('string', 'busName')
    ]),
]);
// -------------------------------- send ----------------------------------- //
const nodeImplementationSend = {
    initialization: ({ node: { args }, state }) => ast `
            ${ConstVar(variableNames.stateClass, state, `{
                busName: "${args.busName}",
            }`)}
        `,
    messageReceivers: ({ state, }) => ({
        '0': AnonFunc([Var('Message', 'm')]) `
            msgBusPublish(${state}.busName, m)
            return
        `,
        '1': coldStringInlet(`${state}.busName`)
    }),
    dependencies: [
        messageBuses,
        commonsWaitEngineConfigure,
        nodeCore,
    ],
};
// -------------------------------- receive ----------------------------------- //
const nodeImplementationReceive = {
    initialization: ({ node: { args }, snds }) => ast `
            commons_waitEngineConfigure(() => {
                msgBusSubscribe("${args.busName}", ${snds.$0})
            })
        `,
    dependencies: [
        messageBuses,
        commonsWaitEngineConfigure,
    ],
};
const builders = {
    send: builderSend,
    receive: builderReceive,
};
const nodeImplementations = {
    send: nodeImplementationSend,
    receive: nodeImplementationReceive,
};

export { builders, nodeImplementations };
