// ------------------------------- node builder ------------------------------ //
const builderThrow = {
    translateArgs: (pdNode) => ({
        busName: assertOptionalString(pdNode.args[0]) || '',
    }),
    build: () => ({
        inlets: {
            '0': { type: 'signal', id: '0' },
            '0_message': { type: 'message', id: '0_message' },
        },
        outlets: {},
        isPullingSignal: true,
    }),
    configureMessageToSignalConnection: (inletId) => {
        if (inletId === '0') {
            return { reroutedMessageInletId: '0_message' };
        }
        return undefined;
    },
};
const builderReceive = {
    translateArgs: (pdNode) => ({
        busName: assertOptionalString(pdNode.args[0]) || '',
    }),
    build: () => ({
        inlets: {
            '0': { type: 'message', id: '0' },
        },
        outlets: {
            '0': { type: 'signal', id: '0' },
        },
    }),
};
const builderSend = {
    translateArgs: (pdNode) => ({
        busName: assertOptionalString(pdNode.args[0]) || '',
    }),
    build: () => ({
        inlets: {
            '0': { type: 'signal', id: '0' },
        },
        outlets: {},
        isPullingSignal: true,
    }),
};
const builderCatch = {
    translateArgs: (pdNode) => ({
        busName: assertOptionalString(pdNode.args[0]) || '',
    }),
    build: () => ({
        inlets: {},
        outlets: {
            '0': { type: 'signal', id: '0' },
        },
    }),
};
// ------------------------------- generateDeclarations ------------------------------ //
const variableNames = generateVariableNamesNodeType('throw_catch_send_receive_t', ['setBusName']);
const nodeCore = () => Sequence([
    Class(variableNames.stateClass, [
        Var('string', 'busName'),
    ]),
    Func(variableNames.setBusName, [
        Var(variableNames.stateClass, 'state'),
        Var('string', 'busName')
    ], 'void') `
        if (busName.length) {
            state.busName = busName
            resetSignalBus(state.busName)
        }
    `
]);
const initialization = ({ node: { args }, state }) => ast `
        ${ConstVar(variableNames.stateClass, state, `{
            busName: '',
        }`)}

        ${variableNames.setBusName}(${state}, "${args.busName}")
    `;
// --------------------------------- throw~ ---------------------------------- //
const nodeImplementationThrow = {
    initialization: initialization,
    loop: ({ ins, state }) => ast `
        addAssignSignalBus(${state}.busName, ${ins.$0})
    `,
    messageReceivers: ({ state }) => ({
        '0_message': AnonFunc([Var('Message', 'm')], 'void') `
            if (
                msg_isMatching(m, [MSG_STRING_TOKEN, MSG_STRING_TOKEN])
                && msg_readStringToken(m, 0) === 'set'
            ) {
                ${variableNames.setBusName}(${state}, msg_readStringToken(m, 1))
                return
            }
        `
    }),
    dependencies: [
        signalBuses,
        nodeCore,
    ]
};
// --------------------------------- catch~ ---------------------------------- //
const nodeImplementationCatch = {
    initialization: initialization,
    loop: ({ outs, state, }) => ast `
        ${outs.$0} = readSignalBus(${state}.busName)
        resetSignalBus(${state}.busName)
    `,
    dependencies: [
        signalBuses,
        nodeCore
    ],
};
// --------------------------------- send~ ---------------------------------- //
const nodeImplementationSend = {
    initialization: initialization,
    loop: ({ state, ins }) => ast `
        setSignalBus(${state}.busName, ${ins.$0})
    `,
    dependencies: [
        signalBuses,
        nodeCore
    ],
};
// --------------------------------- receive~ ---------------------------------- //
const nodeImplementationReceive = {
    initialization: initialization,
    inlineLoop: ({ state }) => ast `readSignalBus(${state}.busName)`,
    messageReceivers: ({ state }) => ({
        '0': AnonFunc([Var('Message', 'm')]) `
            if (
                msg_isMatching(m, [MSG_STRING_TOKEN, MSG_STRING_TOKEN])
                && msg_readStringToken(m, 0) === 'set'
            ) {
                ${variableNames.setBusName}(${state}, msg_readStringToken(m, 1))
                return
            }
        `
    }),
    dependencies: [
        signalBuses,
        nodeCore,
    ]
};
// ------------------------------------------------------------------- //
const builders = {
    'throw~': builderThrow,
    'catch~': builderCatch,
    'send~': builderSend,
    'receive~': builderReceive,
};
const nodeImplementations = {
    'throw~': nodeImplementationThrow,
    'catch~': nodeImplementationCatch,
    'send~': nodeImplementationSend,
    'receive~': nodeImplementationReceive,
};

export { builders, nodeImplementations };
