// ------------------------------- node builder ------------------------------ //
const makeBuilder = (defaultValue) => ({
    translateArgs: (pdNode) => {
        const value = assertOptionalNumber(pdNode.args[0]);
        return {
            value: value !== undefined ? value : defaultValue,
        };
    },
    build: () => ({
        inlets: {
            '0': { type: 'signal', id: '0' },
            '1': { type: 'signal', id: '1' },
        },
        outlets: {
            '0': { type: 'signal', id: '0' },
        },
    }),
    configureMessageToSignalConnection(inletId, nodeArgs) {
        if (inletId === '0') {
            return { initialSignalValue: 0 };
        }
        if (inletId === '1') {
            return { initialSignalValue: nodeArgs.value };
        }
        return undefined;
    },
});
// ------------------------------- node implementation ------------------------------ //
const nodeImplementations = {
    '+~': {
        inlineLoop: ({ ins }) => ast `${ins.$0} + ${ins.$1}`,
    },
    '-~': {
        inlineLoop: ({ ins }) => ast `${ins.$0} - ${ins.$1}`,
    },
    '*~': {
        inlineLoop: ({ ins }) => ast `${ins.$0} * ${ins.$1}`,
    },
    '/~': {
        inlineLoop: ({ ins }) => ast `${ins.$1} !== 0 ? ${ins.$0} / ${ins.$1} : 0`,
    },
    'min~': {
        inlineLoop: ({ ins }) => ast `Math.min(${ins.$0}, ${ins.$1})`,
    },
    'max~': {
        inlineLoop: ({ ins }) => ast `Math.max(${ins.$0}, ${ins.$1})`,
    },
    'pow~': {
        inlineLoop: ({ ins }) => ast `pow(${ins.$0}, ${ins.$1})`,
        dependencies: [pow],
    },
};
const builders = {
    '+~': makeBuilder(0),
    '-~': makeBuilder(0),
    '*~': makeBuilder(0),
    '/~': makeBuilder(0),
    'min~': makeBuilder(0),
    'max~': makeBuilder(0),
    'pow~': makeBuilder(0),
};

export { builders, nodeImplementations };
