// ------------------------------- node builder ------------------------------ //
const builder = {
    translateArgs: ({ args }) => ({
        minValue: assertOptionalNumber(args[0]) || 0,
        maxValue: assertOptionalNumber(args[1]) || 0,
    }),
    build: () => ({
        inlets: {
            '0': { type: 'signal', id: '0' },
            '1': { type: 'message', id: '1' },
            '2': { type: 'message', id: '2' },
        },
        outlets: {
            '0': { type: 'signal', id: '0' },
        },
    })
};
// ------------------------------- node implementation ------------------------------ //
const variableNames = generateVariableNamesNodeType('clip_t');
const nodeImplementation = {
    initialization: ({ node: { args }, state }) => ast `
            ${ConstVar(variableNames.stateClass, state, `{
                minValue: ${args.minValue},
                maxValue: ${args.maxValue},
            }`)}
        `,
    inlineLoop: ({ ins, state }) => ast `Math.max(Math.min(${state}.maxValue, ${ins.$0}), ${state}.minValue)`,
    messageReceivers: ({ state }) => ({
        '1': coldFloatInlet(`${state}.minValue`),
        '2': coldFloatInlet(`${state}.maxValue`),
    }),
    dependencies: [
        () => Sequence([
            Class(variableNames.stateClass, [
                Var('Float', 'minValue'),
                Var('Float', 'maxValue'),
            ]),
        ])
    ],
};

export { builder, nodeImplementation };
